<template>
  <div class="bindCard">
    <header class="count_top header">
      <img src="../../../assets/shopImage/left_icon.png" alt="" @click="$router.go(-1)">
      <span>银行卡信息</span>
    </header>
    <div class="bind_con">
      <div class="iteList"><label>银行卡号:</label><input type="text" placeholder="请输入银行卡号" v-model="bankCarNum"></div>
      <div class="iteList"><label>开户人姓名:</label><input type="text" placeholder="请输入开户人姓名" v-model="username"></div>
      <div class="iteList"><label>银行名称:</label><input type="text" placeholder="请输入银行名称" v-model="bank"></div>
      <div class="iteList"><label>开户行地址:</label><input type="text" placeholder="例:浙江-杭州-xx" v-model="bankAddress"></div>
      <div class="iteList"><label>开户行支行:</label><input type="text" placeholder="杭州钱江支行" v-model="bankName"></div>
      <div class="iteList"><label>手机号:</label><input type="text" placeholder="请填写手机号" v-model="phone"></div>
      <div class="iteList yzCode"><label>验证码:</label><input type="text" placeholder="验证码" v-model="yzCode"><span class="yaCode_send" @click="sendCord">{{yz_text}}</span></div>
    </div>
    <div class="btn_box" @click="submitBank">
      <span>提交绑定</span>
    </div>
    <div class="tips">
      <p>以上信息请务必填写正确，并确保您所填写的银行信息正确无误，以免影响结账活造成损失</p>
    </div>
    <div class="fixed_mask" v-if="maskShow"></div>
    <!--<transition name="inform">-->
      <!--<inform :text="currentText" @informShow="informShow" v-if="informTip"></inform>-->
    <!--</transition>-->
  </div>
</template>

<script>
  import { Toast,MessageBox,Indicator} from 'mint-ui';
  export default {
    name: 'bindCard',
    data(){
      return {
        bankCarNum:'',// 银行卡号
        username:'', // 开户人
        bank:'', // 身份证
        bankAddress:'',  // 开户行地址
        bankName:'',  // 开户行支行
        phone:'', // 电话号码
        yzCode:'',  // 验证码
        yz_text:'获取验证码', //
        maskShow: false,
        informTip:false,
        currentText:'',
        phoneReg: /^((1[0-9]{1})+\d{9})$/, // 手机正则
        id:'',
        is_true: false
      }
    },
    methods:{
      //获取银行卡号信息
      getCard(id) {
        this.post('home/Withdraw/getBankInfoById', {
          id
        }).then(res => {
            if (res.data.code === 10000) {
              this.phone = res.data.info.tel;
              this.username = res.data.info.name;
              this.bank = res.data.info.bank_name;
              this.bankAddress = res.data.info.bank_address;
              this.bankName = res.data.info.bank_zhihang;
              this.bankCarNum = res.data.info.bank_number;
              this.is_true = res.data.info.is_true;
            }
        })
      },
      sendCord(){
        Indicator.open();
          this.post('home/Withdraw/sendMsg',{
            tel: this.phone,
            type:1
          }).then(res=>{
            Indicator.close();
            if (res.data.code === 10000){
              let sec= 60;
              let that = this
              for(let  i=0; i<=60; i++){
                window.setTimeout(function(){
                  if (sec !== 0) {
                    that.yz_text = `${sec}秒后重新获取`
                    sec--
                  } else {
                    sec = 60 //如果倒计时结束就让  获取验证码显示出来
                    that.yz_text = '获取验证码'
                  }
                }, i * 1000)
              }
            } else {
              Toast({
                message: res.data.msg,
                duration: 1000
              });
            }
          })
      },
      // 保存银行卡
      saveBankCard(){
        this.post('home/Withdraw/updateBankInfo', {
          tel: this.phone,
          name: this.username,
          bank_number: this.bankCarNum,
          bank_name: this.bank,
          bank_address: this.bankAddress,
          bank_zhihang: this.bankName,
          msgCode: this.yzCode
        }).then(res => {
          if (res.data.code === 10000) {
            this.$router.go(-1);
          } else {
            Toast({
              message: res.data.msg,
              duration: 1000
            });
          }
        })
      },
      changeBankCard(id){
        this.is_true = this.is_true ? 1 : 0;
        this.post('home/Withdraw/updateBankInfo', {
          tel: this.phone,
          name: this.username,
          bank_number: this.bankCarNum,
          bank_name: this.bank,
          bank_address: this.bankAddress,
          bank_zhihang: this.bankName,
          msgCode: this.yzCode,
          is_true: this.is_true,
          bank_id:id
        }).then(res => {
          if (res.data.code === 10000) {
            this.$router.push('/bankList');
          } else {
            Toast({
              message: res.data.msg,
              duration: 1000
            });
          }
        })
      },
      // 修改银行卡信息
      submitBank(){
        let isBankNum = !isNaN(this.bankCarNum) && ( this.bankCarNum.length>=16 && this.bankCarNum.length<=19);
        let isPhone = this.check.isPoneAvailable(this.phone);
          if (isPhone && isBankNum && this.username && this.bank && this.bankAddress && this.bankName && this
              .yzCode) {
              if (this.id === '-1') {
                  this.saveBankCard();
              } else {
                this.changeBankCard(this.id);
              }
          } else {
            Toast({
              message: '请核对相关信息',
              duration: 1000
            });
          }
        }
    },
    mounted(){
      this.id = this.$route.params.id;
      if (this.id !== '-1') {
        this.getCard(this.id);
      }
    },
    components:{}
  }
</script>

<style scoped>
  @import "../../../../static/header.css";
  .bindCard {
    min-height: 100vh;
    background-color: #f2f2f2;
  }
  .bind_con{background-color: #fff;}
  .bind_con .iteList{width:100%;padding: 0.3rem 0;display: flex;align-items: center}
  .bind_con .iteList label{width:2rem;text-align: right;display: inline-block;}
  .bind_con .iteList:not(:first-child){border-top: 1px solid #eee;}
  .bind_con input[type='text']{border: none;padding: 0.1rem 0.1rem;font-size: 0.3rem;display: inline-block;flex: 1}
  .bind_con input[type='text']::placeholder{color: #999;}
  .bind_con .iteList select {padding:5px 0 5px 0.2rem;width: 4.5rem;font-size: 0.3rem;}
  .bind_con .yzCode{position: relative}
  .bind_con .yzCode .yaCode_send{position: absolute;top: 0.35rem;right: 0.4rem;background-color: #ff6f00;font-size: 0.28rem;padding: .1rem 0.3rem;border-radius: 10px;color: #fff;min-width: 0.5rem;text-align: center;}
  .btn_box{width: 90%;color: #fff;border-radius: 0.2rem;text-align: center;margin: 0.6rem 5% 0.2rem 5%;letter-spacing: 2px;height: 0.88rem;display: flex;  align-items: center;background-color: #ff6f00}
  .btn_box span {display: inline-block;flex: 1;font-size: 0.3rem;}
  .tips{width: 90%;margin: 0.5rem 5% 0 5%;text-align: center;letter-spacing: 1px;}
  .tips p {padding: 0.2rem 0.5rem;font-size: 0.28rem;color: #999;}
  .fixed_mask{position: fixed;background-color: rgba(0,0,0,.2);height: 100%;top: 0;left: 0;width: 100%;text-align: center;}
</style>



